.name-container {
  display: flex;
  justify-content: center;
  background-color: inherit;
  .name {
    box-sizing: border-box;
    margin: 32px 0px;
    min-width: 0px;
    font-family: 'Courier New';
    font-size: 18px;
    border-width: 1px;
    border-style: solid;
    border-image: initial;
    border-color: var(--link-color, #d84136);
    background-color: inherit;
    letter-spacing: 0.5rem;
    padding: 4px 18px;
    width: auto;
    -webkit-box-align: center;
    align-items: center;
  }
}

.avatar-container {
  box-sizing: border-box;
  margin: 0px 0px 32px;
  min-width: 0px;
  display: flex;
  -webkit-box-pack: center;
  justify-content: center;

  .avatar {
    width: 200px;
    height: 200px;
    border-radius: 100px;
  }
}

.position {
  box-sizing: border-box;
  margin: 0px 0px 4px;
  min-width: 0px;
  font-size: 24px;
  font-family: 'Courier New';
  line-height: 1.25;
  text-align: center;
}

.description {
  box-sizing: border-box;
  margin: 0px;
  min-width: 0px;
  font-size: 16px;
  font-family: 'Courier New';
  line-height: 1.25;
  text-align: center;
  color: var(--text-primary-color, #636363);

  .like-icon {
    box-sizing: border-box;
    margin: 0px;
    min-width: 0px;
    color: var(--link-color, #d84136);
    cursor: default;
  }
}

.remark {
  box-sizing: border-box;
  margin: 32px 0px 0px;
  min-width: 0px;
  display: flex;
  flex-direction: column;
  font-size: 20px;
  width: 100%;
  -webkit-box-align: center;
  align-items: center;
  -webkit-box-pack: center;
  justify-content: center;
}

@media screen and (min-width: 640px) {
  .name {
    margin-top: 64px;
    margin-bottom: 64px;
  }

  .avatar-container {
    margin-bottom: 64px;
  }

  .position {
    font-size: 32px;
    margin-bottom: 8px;
  }

  .description {
    font-size: 20px;
  }

  .remark {
    margin-top: 64px;
  }
}
